import React, {Component} from 'react';
import s from './header.module.scss';
import Background from '../img/bg/me.jpg';

const headerImg = {
  backgroundImage: "url(" + Background + ")",
  backgroundSize: 'cover'
}
const topDescr = {
  heading: 'Привет! Меня зовут Андрей Христоев',
  text: 'Я Front-End разработчик, имею опыт в данной сфере уже более двух лет, по образованию юрист. На данный момент развиваюсь в сторону React.js, в дальнейших планах хочу освоить фреймверки Angular и Vue.js. Данное портфолио создано с применением React.js и модуля CRA (Create React App) c реализацией маршрутизации.'
}

class Header extends Component {
  constructor () {
    super();
    this.state = {
      text: "Привет! Меня зовут Андрей Христоев",
      print: '',
      isHide: false
    }
    this.moveBottom = this.moveBottom.bind(this);
  }

  componentWillMount () {
    this.writeText()
  }

  writeText () {
    const formatedText = this.state.text.split('');
    let print = '';
    const loop = setInterval(() => {
      if (formatedText.length >= 1) {
        if (formatedText[0] === '1') {
          print += '<mark>'
          formatedText.shift()
        } else if (formatedText[0] === '2') {
          print += '<mark>'
          formatedText.shift()
        } else {
          print += formatedText.shift()
          this.setState({ print })
        }
      } else { clearInterval(loop) }
    }, 90)
  }

  moveBottom() {
    const sh = window.innerHeight - 60;
    window.scrollTo({
      top: sh,
      behavior: "smooth"
    });
    this.setState({
      isHide: !this.state.isHide
    })
  }

  render () {
    const {isHide} = this.state;
    return (
      <header className={s.pageHeader} style= { headerImg }>
        <div className="container">
          <div className={s.content}>
            <div className={s.content__inner}>
              <h1 className={`${s.mainHeading} lololo`} dangerouslySetInnerHTML={{__html:this.state.print}}></h1>
              <div className={s.descrText}>
                <p>{topDescr.text}</p>
              </div>
            </div>
          </div>
        </div>
        <span className={`${s.btnDown} ${isHide ? `${s.hide}` : ''}`} onClick={this.moveBottom}></span>
      </header>
    );
  }
}

export default Header;